<template>
  <div class="course">
    <p class="photoShow" :style='{backgroundImage:"url("+ courseInfo.cover +")"}'></p>
    <span class="sign">{{courseInfo.buyCount}}人报名</span>
    <div class="desc df dfdir dfjbw">
      <div class="top">
        <p class="name ellipsis2">{{courseInfo.name}}</p>
        <p class="census">{{courseInfo.teacher.name}} {{courseInfo.teacher.job}}</p>
        <div class="info">
          <span>10月10日.上海</span>
        </div>
      </div>
      <div class="df dfjbw dfaic connect">
        <span class="price"><i>￥</i>{{courseInfo.price}}</span>
        <div class="df">
                    <span
                      class="btn interest"
                      @click.stop="handleCollect"
                    >
                        <span
                          :class='courseInfo.is_collect === "0"? "iconfont collect":"iconfont collectsolid" '></span>
                        感兴趣
                    </span>
          <span
            class="btn enroll"
            @click.stop="handleReport"
          >报名</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import _ from 'lodash'
  import merge from 'webpack-merge'

  export default {
    data() {
      return {
        isMove: false
      }
    },
    props: {
      courseInfo: {
            type: Object,
            default: {}
        },
        showLogin:{
          type: Boolean,
          default: false
        }
    },
    created () {
      const that = this;
      // 这样就能保证 调用方法 只归某个实例拥有
      this.debounceCollect = _.debounce(that.submitCollectData, 300, {
          'leading': true,
          'trailing': false
        }
      );


    },


    mounted() {
    },
    computed: {
      ...mapState({})
    },
    methods: {
      ...mapActions({
        userAuth: 'user/auth',
        saveCollect: 'course/saveCollectById'
      }),

      /**判断是否登陆状态 */
     isLogin(){
       let status = this.$ls.get("CZXY_TOKEN");
       status ?  "": this.$emit("changeShowLogin", true);
       return status
     },

      /**修改感兴趣状态 */
      handleCollect(){
        const status = this.courseInfo.is_collect === "0" ? "1" : "0";
       if(!this.isLogin()){
         return;
       }
        this.debounceCollect(status);
      },

      /**向后端发送收藏请求 */
      async submitCollectData(status){
        let info = {...this.courseInfo, is_collect:status};

        // 先获取employeeId
        let token = this.$ls.get("CZXY_TOKEN");
        if (token) {
          const {data} = await
          this.userAuth({username: token});
          const params = {
            employeeId: data.data.id,
            catalogId: info.id
          }
          const {data:{status}} = await
          this.saveCollect(params)
          if (status === 200) {
            // 调用结束后传给父集
            this.$emit("changeCollectStatus", info);
          }
        }
        // 测试代码
        // this.$emit("changeCollectStatus", info);
      },

      /** 点击报名状态 */
      handleReport(){
       if(!this.isLogin()){
         return;
       }
        let info = {...this.courseInfo};
        // 调用结束后传给父集
        this.$emit("changeHandleReport", info);
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .course {
    margin-top: 50px;
    // height: 440px;
    position: relative;
    overflow: hidden;
    box-shadow:0px 10px 10px 0px rgba(0,0,0,0.05);
    user-select: none;
    .photoShow {
      width: 100%;
      height: 206px;
      display: block;
      box-sizing: border-box;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
    }
    .sign {
      position: absolute;
      left: 0;
      top: 0;
      font-size: 16px;
      background: #E2AD87;
      color: #fff;
      padding: 6px 14px;
      border-bottom-right-radius: 8px;
    }
    .desc {
      width: 100%;
      background: #fff;
      padding: 24px 16px 26px;
      box-sizing: border-box;
      height: 270px;
      .name {
        font-weight: bold;
        font-size: 24px;
        line-height: 33px;
        color: #333;
      }
      .census {
        margin: 6px 0 20px;
        font-size: 16px;
        color: #777;
      }
      .info {
        overflow: hidden;
        span {
          float: left;
          font-size: 14px;
          color: #06609A;
          line-height: 36px;
          padding: 0 15px;
          margin-right: 10px;
          border-radius: 20px;
          border: 1px solid #06609A;
        }
      }
      .connect {
        margin-top: 24px;
        .price {
          color: #E2AD87;
          font-size: 36px;
          i {
            font-style: normal;
            font-size: 24px;
          }
        }
        div {
          .btn {
            cursor: pointer;
            padding: 0 16px;
            text-align: center;
            line-height: 42px;
            font-size: 16px;
            border-radius: 21px;
          }
          .interest {
            color: #E2AD87;
            background: #F7EFE9;
            margin-right: 10px;
            &:hover {
              background: #F5E5D7;
            }
          }
          .enroll {
            padding: 0 32px;
            color: #fff;
            font-size: 20px;
            background: #06609A;
            &:hover {
              background: #055385;
            }
          }
        }
      }
    }

  }


</style>



