<template>
  <div class="cover-wrapper" style="display: block;">
    <div class="pay-box">
      <p class="title">手机扫码购买</p>
      <div class="code-img" v-html="qrcodeUrl"></div>
      <span class="close iconfont" @click="close"></span>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';
  import fetch from '@/api/fetch';

  export default {
    data() {
      return {
        qrcodeUrl: ''
      }
    },
    props: {
      courseId: {
        desc: '课程购买ID',
        type: String,
        default: ''
      },
      from: {
        desc: '渠道码',
        type: String,
        default: ''
      }
    },
    created() {
      this.qrcodeUrl = '';
      this.payment();
    },
    methods: {
      /**
       * 扫码支付
       */
      payment() {
        fetch.get('/payment/qrcode', {
          courseId: this.courseId || '',
          from: this.from || '20191024001'
        }).then(res => {
          this.qrcodeUrl = res.data;
        })
      },
      close() {
        this.$emit('close')
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .cover-wrapper {
    opacity: 1;
    background-color: rgba(0, 0, 0, .5);
    position: fixed;
    bottom: 0;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 10001;
    .pay-box {
      width: 440px;
      max-width: 80%;
      height: 430px;
      background: #fff;
      border-radius: 6px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      text-align: center;
      .title {
        margin-top: 80px;
        font-size: 28px;
      }
      .code-img {
        margin: 34px auto 0;
        width: 200px;
        height: 200px;
        svg {
          width: 100%;
          height: 100%;
        }
      }
      .close {
        position: absolute;
        right: 12px;
        top: 12px;
        font-size: 20px;
        padding: 20px;
        cursor: pointer;
        color: #777777;
      }
    }
  }
</style>

<style>
  .code-img svg {
    width: 100%;
    height: 100%;
  }
</style>



