<template>
  <section class="container">
    <!-- 大图导航栏 -->
    <div class="bgPicture" :style='{backgroundImage:"url("+ picture.bgPic +")"}'>
      <el-row :gutter="10">
        <el-col :offset="2" :xs="22" :sm="22" :md="14" :lg="14" :xl="14">
          <div class="introduce">
            <h3 class="title">一站式企业内训服务商</h3>
            <p class="desc">专注职场培训，通过线上线下结合的方式，让企业培训更简单，员工学习更有效</p>
            <ul class="speciality">
              <li> “丰富实战经验+科学方法论”的资深讲师</li>
              <li>一对一专属培训顾问，提供培训解决方案</li>
              <li>2000节+线上精品课程</li>
            </ul>
          </div>
        </el-col>
        <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <div class="contact df dfdir dfaic">
            <p class="limit">联系我们</p>
            <p class="phone">1523212532</p>
            <span class="findBusiness" @click="findBusiness">找企业内训</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 合作企业 -->
    <div class="cooperation" :style='{backgroundImage:"url("+ picture.cooperationPic +")"}'>
        <h3>截止目前，我们已累计服务<span class="sign">23212</span>家企业，覆盖<span class="sign">40万+</span>多职场人</h3>
        <div class="swiperAuto">

          <swiper
            :options="swiperOption"
            ref="mySwiper"
            class="swiperCon"
            @slideChange="slideChange"
            v-if="businessList && businessList.length>0"
          >
            <swiper-slide v-for="(items,index) in businessList" :key="items+index">
                <div class="slide-box">
                    <el-row :gutter="20">
                        <el-col
                            class="company"
                            v-for="(business, num) in items" :key="business+num"
                            :xs="12" :sm="8" :md="8" :lg="4" :xl="4">
                            <div class="df dfc">
                              <p class="icon" :style="{backgroundImage: business.imgSrc}"></p>
                            </div>
                        </el-col>
                    </el-row>
              </div>
            </swiper-slide>
          </swiper>
            <!-- 切换按钮 -->
            <div :class="currentSwiperIndex === 0 ? 'swiper-btn-prev btn-disabled': 'swiper-btn-prev'">
                <span class="iconfont prev"></span>
            </div>
            <div :class="currentSwiperIndex === businessList.length-1? 'swiper-btn-next btn-disabled':'swiper-btn-next'">
                <span class="iconfont next"></span>
            </div>
        </div>
    </div>
    <!-- 内训讲师 -->
    <div class="mode teachers" v-if="teacher.data && teacher.data.data">
      <div class="top df dfjbw dfaic">
        <h3 class="title">内训讲师<span class="subTitle">为您提供优质的培训方案</span></h3>
        <!-- <span class="more">更多课程</span> -->
      </div>
      <div class="lecturer df">
        <!-- <el-row :gutter="40">
          <el-col
            :xs="24" :sm="24" :md="12" :lg="6" :xl="6"
            
          > -->
            <lecturer
              v-for="(t, index) in teacher.data.data" :key="t + index"
              :teacherInfo="t"
              @click.native="seeTeacherDetail(t)" />
          <!-- <</el-col>
        </el-row>-->
      </div>
    </div>

    <!-- 线下课 -->
    <div class="mode classes" v-if="courseList.data && courseList.data.list">
      <div class="top df dfjbw dfaic">
        <h3 class="title">线下课<span class="subTitle">精品课任你挑选</span></h3>
        <!-- <span class="more">更多课程</span> -->
      </div>
      <div class="course">
        <el-row :gutter="40">
          <el-col
            :xs="24" :sm="24" :md="12" :lg="8" :xl="8"
            v-for="(course, index) in courseList.data.list" :key="course + index"
          >
            <course
              :courseInfo="course"
              :showLogin="showLogin"
              @click.native="seeCourseDetail(course)"
              @changeShowLogin="changeShowLogin"
              @changeCollectStatus="changeCollectStatus"
              @changeHandleReport="changeHandleReport"
            />
          </el-col>
        </el-row>
      </div>
    </div>

    <!-- 提交信息 -->
    <div class="subInfo" id="perfectInfo">
      <p class="tips">如有任何企业培训需求，请留言说明，我们将为您提供更多方案建议</p>
      <div class="df dfc selectRadio">
        <el-radio v-model="requestParams.consultType" label="1" style="margin-right: 50px;"><span class="radioName">企业咨询</span></el-radio>
        <el-radio v-model="requestParams.consultType" label="2"><span class="radioName">个人咨询</span></el-radio>
      </div>
      <div class="competeInfo">
        <el-row :gutter="15">
          <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="df filled">
            <input type="text" ref="companyName" v-model="requestParams.companyName" class="companyName df1" placeholder="企业名称"/>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df filled">
              <el-select
                class="industry df1"
                v-model="requestParams.industry"
                placeholder="请选择行业"
              >
                <el-option
                  v-for="ind in industryList"
                  :key="ind.value"
                  :label="ind.label"
                  :value="ind.value">
                </el-option>
              </el-select>
          </el-col>
        </el-row>
        <el-row :gutter="15">
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df filled">
            <input type="text" ref="name" v-model="requestParams.name" class="name df1" placeholder="姓名"/>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df filled">
            <input type="text" maxLength='11'
              ref="mobile"
              v-model="requestParams.mobile"
              class="phone df1"
              placeholder="手机号"
              oninput = "value=value.replace(/[^\d]/g,'')"
            />
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df filled">
            <input type="text" ref="job" v-model="requestParams.job" class="position df1" placeholder="职位"/>
          </el-col>
        </el-row>
        <el-row :gutter="15">
          <el-col :span="24" class="df">
            <textarea rows="6" v-model="requestParams.content" class="courseNeed df1" style="resize:none;" placeholder="课程需求"/>
          </el-col>
        </el-row>
      </div>
      <button class="submitInfo" @click="submitData">提交</button>
    </div>

    <div class="Modular"></div>
    <!-- 注册领取福利 -->
    <div class="getGift df dfjbw dfaic" v-if="true">
      <div class="df dfaic">
        <img :src="picture.giftPic" class="giftPic">
        <div class="sign">
          <p>注册领福利</p>
          <span>线上2102节课程免费畅享·1对1专属顾问服务</span>
        </div>
      </div>
      <div class="df dfaic">
        <p class="haveGift"><span class="">12232</span>家企业已领取</p>
        <button class="freeGet" @click="getGiftForfree">立即免费领取</button>
      </div>
    </div>
    <PaymentPopup v-if="showPayPopup" :courseId="courseId" :from="from" @close="showPayPopup=false"/>

    <!-- 登录 -->
    <Login v-if="showLogin" @close="showLogin=false" @showSuccessfully="successfully=true"></Login>

    <!-- 领取礼包成功 -->
    <Successfully v-if="successfully"></Successfully>
  </section>
</template>
<script>
  import {mapState, mapMutations,mapActions} from 'vuex'
  import Lecturer from '../../components/Lecturer'
  import Course from '../../components/Course'
  import PaymentPopup from '../../components/PaymentPopup'
  import Successfully from '../../components/Successfully'
  import Login from '../../components/Login'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import merge from 'webpack-merge'
  import {cooperationList, industryList} from '../../config/base'
  import 'swiper/dist/css/swiper.css'

  const baseReq = {
    consultType: "1",  //选中的单选框，默认是企业
    companyName: "",
    industry: null,
    name: "",
    mobile: "",
    job: "",
    content: ""
  }
  export default {
    data() {
      return {
        showLogin: false,
        picture:{
          bgPic: require("../../assets/images/bgPic.png"),
          cooperationPic: require("../../assets/images/cooperationPic.png"),
          giftPic:require("../../assets/images/gift.png")
        },
        swiperOption: {  //轮播配置
          navigation: {
            prevEl: '.swiper-btn-prev',
            nextEl: '.swiper-btn-next'
          },
          loop: false
        },
        navIndex: 0,
        businessList: this.filterArr(cooperationList), //服务企业列表
        currentSwiperIndex: 0, //当前轮播下标
        lecturerList:[],
        requestParams: JSON.parse(JSON.stringify(baseReq)), //请求参数

        industryList,

        showPayPopup: false, // 购买二维码
        payInfo: {},

        // 领取礼物成功
        successfully: false
      }
    },
    computed: {
      ...mapState({
        teacher: state => state.teachers.teacherList,
        courseList: state => state.course.excellentCourseList
      }),
    },
    created() {
      document.title = `一站式企业内训服务商 - 插座学院`
      // 获取讲师列表
      this.getTeacher();
      // 获取精品课课程
      this.getCourseList();
    },
    methods: {
      ...mapMutations({
        changeCourse: 'course/CHANGE_COURSE'
      }),
      ...mapActions({
        getTeacherList: 'teachers/getTeachersList',
        getCourseList: 'course/getCourseList',
        saveCompanyInfo: 'course/saveCompanyInfo'
      }),
      /**
       * 获取讲师
       *
       * @returns 讲师列表
       */
      async getTeacher() {
        const {page, desc, category_id, keyword} = this.$route.query;

        await this.getTeacherList({
          page,
          desc,
          keyword,
          category_id
        });
      },


      /**
       * 切换页码
       * @page 页码
       */
      async changePage(page) {
        this.$router.replace({
          query: merge(this.$route.query, {
            page
          })
        });
        this.getTeacher();
      },

      /**
       * 切换讲师排序方式
       * @desc 排序方式 created_at || browse
       * @index 排序索引
       */
      async changeArticleDesc(desc, index) {
        this.$router.replace({
          query: merge(this.$route.query, {
            desc
          })
        });
        this.navIndex = index;
        this.getTeacher();
      },

      /**swiper滑动 */
      slideChange(){
        this.currentSwiperIndex = this.$refs.mySwiper.swiper.activeIndex;
      },

      /***校验请求的数据 */
      validateRequest(){
        const {
          companyName,
          industry,
          name,
          mobile,
          job,
          content
        } = this.requestParams;

        /**数据为空的验证 */
        if(!companyName.trim()){
          this.$message({
            message: '请填写企业名称',
            type: 'warning'
          });
          this.$refs.companyName.focus();
          return false
        }
        if(!industry){
          this.$message({
            message: '请选择行业',
            type: 'warning'
          });
          return false
        }
        if(!name.trim()){
          this.$message({
            message: '请填写姓名',
            type: 'warning'
          });
          this.$refs.name.focus();
          return false
        }
        if(!mobile.trim()){
          this.$message({
            message: '请填写手机号',
            type: 'warning'
          });
          this.$refs.mobile.focus();
          return false
        }
        if(!job.trim()){
          this.$message({
            message: '请填写职位名称',
            type: 'warning'
          });
          this.$refs.job.focus();
          return false
        }

        return true;
      },
      /**提交数据 */
      async submitData(){
        const status = this.validateRequest();
        if(!status){
          return;
        }
        const baseInfo = {
          employeeCount: "",
          duty: "",
          peopleNum: 0,
          chennel: "",
          type: 4,
          from: 'APP'
        }
        const {data} = await this.saveCompanyInfo({...baseInfo, ...this.requestParams});
        if(data.status === "200"){
          this.requestParams = baseReq;
          this.$message({
            message: '信息填写成功',
            type: 'success'
          });
        }
      },
        /**企业案例数组转化 */
      filterArr(list){
        const index = list.length / 6 > 0 ? Math.ceil(list.length / 6) : list.length / 6;
        let newList= [];
        for(let i=0;i<index;i++){
            newList.push(list.splice(0, 6));
        }
        return newList;
      },

      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        let token = this.$ls.get("CZXY_TOKEN");
        if (token) {
          this.$router.push(path);
        }else{
          this.changeShowLogin(true);
        }
      },

      /**跳转到讲师详情 */
      seeTeacherDetail(item){
        this.toPath('/training/lecturer/'+ item.id);
      },

      /**跳转到课程详情 */
      seeCourseDetail(item){
        this.toPath('/training/courses/'+ item.id);
      },

      /* 修改是否显示登陆弹窗状态*/
      changeShowLogin(bool){
        this.showLogin = bool;
      },

      /**修改感兴趣状态 */
      changeCollectStatus(item){
        let list = this.courseList.data.list;

        const order = list.findIndex(course => course.id === item.id);
        list.splice(order, 1, item);
        this.changeCourse(list);

      },

      /**
       * 购买
       */
      changeHandleReport(item){
        this.showPayPopup = true
        this.courseId = item.pay_id
        this.from = ''
      },

      /* 未登录领取礼物，出现登录弹窗*/
      getGiftForfree(){
        let token = this.$ls.get("CZXY_TOKEN");
        if (!token) {
          this.changeShowLogin(true);
        }
      },

      /**找锚点滚动到相应位置 */
      findBusiness(){
        document.querySelector('#perfectInfo').scrollIntoView(true);
      }
    },
    components: {
      Login,
      swiper,
      Course,
      Lecturer,
      swiperSlide,
      PaymentPopup,
      Successfully,
    }
  }
</script>

