  .page {
    padding: 32px 0;
    text-align: center;
  }

    /**修改原组件的样式 */
    .selectRadio /deep/ .el-radio__input.is-checked .el-radio__inner {
        border-color: #06609A;
        background: #06609A;
    }

    .selectRadio /deep/ .el-radio__input.is-checked+.el-radio__label{
        color: #06609A;
    }
    .industry /deep/ .el-input .el-input__inner{
      background: #f7f7f7;
      height: 64px;
      line-height: 64px;
      border-radius: 32px;
      font-size: 24px;
      padding-left: 30px;
      color: #666;
      border: none;
    }
    .industry /deep/ .el-input .el-input__suffix{
      right: 20px;
      .el-input__icon{
        line-height: 64px;
      }
    }

    /** 修改placeholder的颜色 */
    .industry /deep/ .el-input .el-input__inner::-webkit-input-placeholder{
      color: #666;
    }
    .industry /deep/ .el-input .el-input__inner:-moz-placeholder{
      color:#666;
    }
    .industry /deep/ .el-input .el-input__inner::-moz-placeholder{
      color:#666;
    }
    .industry /deep/ .el-input .el-input__inner:-ms-input-placeholder{
      color:#666;
    }



