<template>
  <!-- 领取礼包成功 -->
  <div class="cover-wrapper">
    <div class="pay-box">
      <p class="title">
        <span class="iconfont accomplish"></span> 领取成功
      </p>
      <p class="p2">价值5999元课程礼包</p>
      <img class="code-img" :src="downloadPic"/>
      <p class="p3">微信扫码，下载好多课APP进行查看</p>
      <span class="close iconfont" @click="close"></span>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {
    data() {
      return {
        downloadPic: require("@/assets/images/footer/download.png"),
      }
    },
    props: {
    },
    created() {
    },
    methods: {
      close() {
//        this.$emit('close')
        setTimeout(()=> {
          window.location.href = '/'
        }, 100)
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  /* 领取礼包 */
  .cover-wrapper {
    opacity: 1;
    background-color: rgba(0, 0, 0, .5);
    position: fixed;
    bottom: 0;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 10001;
    .pay-box {
      width: 440px;
      max-width: 80%;
      height: 460px;
      background: #fff;
      border-radius: 6px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      text-align: center;
      line-height: 1;
      .title {
        font-weight: 500;
        margin-top: 80px;
        margin-bottom: 15px;
        font-size: 28px;
      }
      .accomplish {
        color: #1DD189;
        font-size: 29px
      }
      .p2 {
        font-size: 24px;
        color: #666;
      }
      .p3 {
        margin-top: 10px;
        font-size: 18px;
        color: #5B5B5B;
      }
      .code-img {
        margin: 34px auto 0;
        width: 200px;
        height: 200px;
        svg {
          width: 100%;
          height: 100%;
        }
      }
      .close {
        position: absolute;
        right: 12px;
        top: 12px;
        font-size: 20px;
        padding: 20px;
        cursor: pointer;
        color: #777777;
      }
    }
  }
</style>



